    verificarLogin() {
      return new Promise(resolve => {
      this.storage.get('usr_tok_by').then((value) => {
   // console.log(value);
    value ? resolve(value) : resolve(false);
    }).catch(() => resolve(false))
    });
  }


    getCarrito() {
      return new Promise(resolve => {
      this.storage.get(`carrito`).then((value) => {
   // console.log(value);
    value ? resolve(value) : resolve([]);
    }).catch(() => resolve(false))
    });
  }


    getTotal() {
      return new Promise(resolve => {
      this.storage.get(`carrito`).then((value) => {
   // console.log(value);

   let total = 0;
   

    value.forEach((elementw, index) => {
    total += elementw.precio;
    });


    resolve(total);

    }).catch(() => resolve(false))
    });
  }



          addProducto(producto) {
      return new Promise(resolve => {
       
         this.storage.get('carrito').then((value) => {
         if(value){
         value.push(producto);
         this.storage.set(`carrito`, value);
         resolve(value);
        }
        else{
          let carrito = [];
          carrito.push(producto);
          this.storage.set(`carrito`, carrito);
          resolve(carrito);
        }
        }).catch(() => resolve(false))
        });
    }


          sacarProducto(servicio) {
      return new Promise(resolve => {
       
         this.storage.get(`carrito`).then((value) => {
         if(value){
         //let index = value.indexOf(idServicio);
         let index = value.findIndex(i => i.idServicio === servicio);
            if (index > -1) {
            value.splice(index, 1);
            }
         console.log(value);
         this.storage.set(`carrito`, value);
         resolve(value);
        }
        else{
          resolve([]);
        }
        }).catch(() => resolve(false))
        });
    }

          vaciarCarrito() {
      return new Promise(resolve => {
       
         let carrito = [];
          this.storage.set(`carrito`, carrito);
          resolve(carrito);


        });
    }



  //this.storage.set(`usr_tok_datagym`, data.data[0]);


